import { useState, useRef } from "react";
import { useSelector } from "react-redux";
import { Tab } from "@ya.praktikum/react-developer-burger-ui-components";
import DraggableIngredient from "./draggable-ingredient.jsx";
import styles from "./burger-ingredients.module.scss";

const BurgerIngredients = () => {
  const [current, setCurrent] = useState("bun");
  const containerRef = useRef(null);
  const bunRef = useRef(null);
  const sauceRef = useRef(null);
  const mainRef = useRef(null);
  const ingredientsData = useSelector((store) => store.burgerReducer.ingredientsData);

  const onTabClick = (ref) => {
    ref.current.scrollIntoView({ block: "start", behavior: "smooth" });
  };

  const onContainterScroll = () => {
    const scrollTop = containerRef.current.scrollTop;
    const bunHeight = bunRef.current.clientHeight;
    const sauceHeight = sauceRef.current.clientHeight;

    if (scrollTop < bunHeight / 2) {
      setCurrent("bun");
    } else if (scrollTop > bunHeight / 2 && scrollTop < bunHeight + sauceHeight / 2) {
      setCurrent("sauce");
    } else {
      setCurrent("main");
    }
  };

  return (
    <section className={`${styles.burgerIngredients} pt-10 mr-10`}>
      <h1 className={"text text_type_main-large"}> </h1>
      <div className={`${styles.tab} mt-5`}>
        <Tab
          value="bun"
          active={current === "bun"}
          onClick={(evt) => {
            setCurrent(evt);
            onTabClick(bunRef);
          }}
        >
          
        </Tab>
        <Tab
          value="sauce"
          active={current === "sauce"}
          onClick={(evt) => {
            setCurrent(evt);
            onTabClick(sauceRef);
          }}
        >
          
        </Tab>
        <Tab
          value="main"
          active={current === "main"}
          onClick={(evt) => {
            setCurrent(evt);
            onTabClick(mainRef);
          }}
        >
          
        </Tab>
      </div>
      <div className={styles.container} onScroll={onContainterScroll} ref={containerRef}>
        {ingredientsList(
          ingredientsData.filter((item) => item.type === "bun"), //    
          "",

          bunRef
        )}

        {ingredientsList(
          ingredientsData.filter((item) => item.type === "sauce"), //    
          "",

          sauceRef
        )}

        {ingredientsList(
          ingredientsData.filter((item) => item.type === "main"), //    
          "",

          mainRef
        )}
      </div>
    </section>
  );
};

const ingredientsList = (ingredients, type, tabRef) => {
  return (
    <ul className={styles.ingredientsWrap} ref={tabRef}>
      <h2 className={`text text_type_main-medium pt-10`}>{type}</h2>
      <ul className={`${styles.ingredientsList} mt-6`}>
        {ingredients.map((ingredient) => (
          <DraggableIngredient key={ingredient._id} ingredient={ingredient} />
        ))}
      </ul>
    </ul>
  );
};

export default BurgerIngredients;
